import { useQuery } from "react-query";
import { IGetMovies, IMovie, getMovies } from "../../api/api";
import { makeImageUrl } from "../../utils";
import {
    Wrapper, Banner, Title, Loader, Overview, Slider, Row,
    rowVars, Box, boxVars, infoVars, Info, MovieDetailContainer
} from "./HomeStyled";
import { styled } from "styled-components";
import { AnimatePresence, motion } from "framer-motion";
import { useState } from "react";
import { Outlet, useMatch, useNavigate } from "react-router-dom";
import { useSetRecoilState } from "recoil";
import { movieItemAtom as m } from "../../common/atom";





const Home = () => {
    const offset = 6;
    const [index, setIndex] = useState(0);
    const navigate = useNavigate();
    const { data: movies, isLoading } =
        useQuery<IGetMovies>(["movie", "now_playing"], getMovies);
    const firstMovie = movies?.results[0];
    const incIndex = () => setIndex(prev => {
        const movieLen = movies?.results.length || 0;
        // 6    if (movieLen-1 > 0 && Math.trunc((movieLen-1) / offset)-1 > prev) {
        if (movieLen-1 > 0 && (movieLen-1)> prev) { 
            return prev + 1;
        }
        return prev;
    });

    // let movieLayoutId: string | null = null;
    const setMovieItem = useSetRecoilState<IMovie | null>(m);
    const onClickMovie = (movie: IMovie) => {
        setMovieItem(movie);
        navigate(`movie/${movie.id}`, {
            state: {
                posterPath: movie.poster_path
            }
        });
    }
    
    const movieRouted = useMatch("movie/:movieId");
    console.log('movieRouted: ', movieRouted?.params["movieId"]);

    return <Wrapper>

        <Banner onClick={incIndex} bgImg={makeImageUrl(firstMovie?.backdrop_path || '')}>
        {isLoading ? <Loader>Loading...</Loader> : <>
            <Title>{firstMovie?.title}</Title>
            <Overview>{firstMovie?.overview}</Overview>
            </>}
        </Banner>
        <Slider>
            <AnimatePresence initial={false}>
                <Row key={index}    // index   
                    variants={rowVars} //     
                    initial={"initial"}
                    animate={"start"}
                    exit={"end"}
                    transition={{
                        type: "linear",
                        duration: 1
                    }}
                >
                    {
                        // movies 6  
                        movies ? movies!
                            .results
                            .slice(1)
                            .slice(index * offset, index * offset + offset)
                            .map(m =>
                                <AnimatePresence>
                                    <Box 
                                        onClick={() => onClickMovie(m)}
                                        variants={boxVars}
                                        initial={"initial"}
                                        whileHover={ movieRouted ? "initial" : "zoom"}
                                        transition={{ type: "linear" }}
                                        layoutId={`${m.id}`}
                                        bgImg={makeImageUrl(m.poster_path, "w500")}>
                                        { <Info variants={infoVars}>
                                            <h2>{m.original_title}</h2>
                                        </Info> }
                                    </Box>
                                </AnimatePresence>)
                            : null
                    }
                    
                </Row>
            </AnimatePresence>
            
        </Slider>

        {/* MovieDetail's container */}
        <Outlet />

    </Wrapper>;
}

export default Home;