const BASE_URL = 'https://api.themoviedb.org/3';
const API_KEY = "c610b9320ba06d7f9a12ef4ed959de27";

export interface IMovie {
    adult: boolean,
    backdrop_path: string,
    genre_ids: []
    id: number,
    original_language: string,
    original_title: string,
    overview: string,
    popularity: number,
    poster_path: string,
    release_date: string,
    title: string,  
    video: boolean  
    vote_average: number, 
    vote_count: number,
};


export interface IGetMovies {
    dates: {
        maximum: string,
        minimum: string
    }
    page: number,
    results: IMovie[],
    total_pages:number,
    total_results:number,
}

export interface IMovieDetail {
    adalt: boolean,
    backdrop_path: string,
    budget: number,
    homepage: string, 
    id: number, 
    imdb_id: string,
    original_title: string,
    overview: string,
    poster_path: string,
    status: string,
    title: string,
    vote_average: number,
    vote_count: number,
}

export const getMovies = () => {
    return fetch(`${BASE_URL}/movie/now_playing?api_key=${API_KEY}`)
        .then(response => response.json());
}

export const getMovieDetail = (id: number) => {
    return fetch(`${BASE_URL}/movie/${id}?api_key=${API_KEY}`)
        .then(response => response.json());
}

