import { AnimatePresence } from "framer-motion";
import CloseIcon, { Box, DetailTitle, HomePage, Loader, MovieDetailContainer, Overlay, boxVars } from "./HomeStyled";
import { IMovieDetail, getMovieDetail } from "../../api/api";
import { makeImageUrl } from "../../utils";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useQuery } from "react-query";

const MovieDetail = () => {
    const navigate = useNavigate(); 
    const { movieId } = useParams();
    const { posterPath } = useLocation().state;
    const { data: movie, isLoading } = useQuery<IMovieDetail>(["movie", "detail"], () => getMovieDetail(parseInt(movieId!)));
    const onClose = () => {
        navigate("/");
    }
    return <Overlay onClick={onClose}>
        {isLoading ?
            <MovieDetailContainer><Loader></Loader></MovieDetailContainer> :
            <MovieDetailContainer className="movie_detail">
                <AnimatePresence>
                    <Box
                        variants={boxVars}
                        initial={"initial"}
                        animate={"popup"}
                        transition={{type: "linear"}}
                        layoutId={`${movie!.id}`}
                        bgImg={makeImageUrl(posterPath, "w500")}
                    >
                        <DetailTitle>{movie?.title}</DetailTitle>
                        <CloseIcon onClose={onClose} />
                        <HomePage>{movie!.homepage}</HomePage>
                    </Box>
                    
                    
                </AnimatePresence>
            </MovieDetailContainer>}
    </Overlay>;
}

export default MovieDetail;