import { BrowserRouter, Navigate, Route, Routes, createBrowserRouter } from "react-router-dom"
import Home from "./routes/home/Home"
import TVShow from "./routes/TVShow"
import Search from "./routes/search/Search"
import App from "./App"
import MovieDetail from "./routes/home/MovieDetail"

interface IProtectedRoute {
    children: any,
    isAuthenticated: boolean
}
const ProtectedRoute: React.FC<IProtectedRoute> = ({ children, isAuthenticated }) => {
    if (!isAuthenticated) {
      //     
      return <Navigate to="/" />;
    }
    return children;
};

const router = createBrowserRouter([{
    path: "/",
    element: <App />,
    children: [
        {
            path: "/",
            element: <Home />,
            children: [
                {
                    path: "movie/:movieId",
                    element: <MovieDetail />
                },
            ]
        },
        {
            path: "tv",
            element: <TVShow />
        },
        {
            path: "search/:keyword",
            element: <ProtectedRoute isAuthenticated={false}><Search /></ProtectedRoute>
        },
    ]

}]);

export default router;